<template>
  <div class="user">
    <div class="user-search">
      <div class="user-search-title">超级会员激活</div>
      <div class="user-search-input">
        <van-field
          class="input"
          v-model="mobile"
          type="tel"
          placeholder="输入手机号"
        />
        <van-button class="button" round type="info" @click="onButton"
          >搜索</van-button
        >
      </div>
    </div>
    <!-- item -->
    <div class="user-item" v-if="userItem">
      <div
        class="user-item-content"
        v-for="(item, index) in userItem"
        :key="index"
      >
        <div class="title">
          <div class="title-name">{{ item.nickName }}</div>
          <div class="title-phone">
            <span style="vertical-align: super; margin-right: 12px">{{
              item.mobile
            }}</span
            ><van-icon
              name="envelop-o"
              size="26"
              color="#2176CF"
              @click="showPopup(index)"
            />
          </div>
        </div>
        <div class="context">
          <div class="context-left">
            <van-cell
              class="cell"
              title="ID"
              title-class="cell-left"
              value-class="cell-right"
              :value="item.userId"
            />
            <van-cell
              class="cell"
              title="会员类型"
              title-class="cell-left"
              value-class="cell-right"
              :value="item.superVipName"
            />
            <van-cell
              class="cell"
              title="最新购买时间"
              title-class="cell-left"
              value-class="cell-right"
              :value="item.lastBuyTime"
            />
          </div>
          <div
            class="context-right"
            v-if="item.superVipStatus === 1"
            style="background: #ff770f"
          >
            <span @click="onUpdata(item.mobile)">待激活</span>
          </div>
          <div
            class="context-right"
            v-if="item.superVipStatus === 2"
            style="background: #2176cf"
          >
            <span>已激活</span>
          </div>
        </div>
        <div class="button">
          <van-cell
            class="cell"
            title="生日"
            title-class="cell-left"
            value-class="cell-right"
            :value="item.birthday"
          />
          <van-cell
            class="cell"
            title="地址"
            title-class="cell-left"
            value-class="cell-right"
            :value="item.receiptAddress"
          />
        </div>
      </div>
    </div>
    <!-- 空白页 -->
    <div class="bg" v-else>
      <img src="@/assets/white-bg.png" />
    </div>
    <!-- 发送短信 -->
    <van-popup class="message" v-model="show">
      <div v-if="userItem[index]">
        <div class="message-title">
          <van-icon name="envelop-o" size="22" color="#fff" />
          <span style="margin-left: 8px">{{ userItem[index].nickName }}</span>
          <span style="margin-left: 8px">{{ userItem[index].mobile }}</span>
        </div>
        <div class="message-content">
          <van-field
            class="text"
            v-model="message"
            rows="1"
            autosize
            type="textarea"
            placeholder="请输入留言"
          />
          <van-button class="button" round type="info" @click="onMessage(userItem[index].mobile,message)"
            >发送</van-button
          >
        </div>
      </div>
    </van-popup>
  </div>
</template>
<script>
import { vipListApi, updateApi } from "@/api/superVip";
import { sendMessage } from "@/api/message";

export default {
  data() {
    return {
      mobile: "",
      page: 1,
      size: 10,
      userItem: "",
      show: false,
      index: 0,
      message: "",
    };
  },
  methods: {
    // 查找
    onButton() {
      // if (!this.mobile) return;
      vipListApi(this.mobile, this.page, this.size).then((res) => {
        this.userItem = res.data.data.items;
        console.log(this.userItem);
      });
    },
    // 激活超级会员
    onUpdata(mobile) {
      // console.log(this)

      this.$dialog
        .confirm({
          title: "超级会员",
          message: "是否激活超级会员",
        })
        .then(() => {
          // on confirm
          updateApi(mobile)
            .then((res) => {
              this.$toast("激活成功");
              console.log(res.data);
            })
            .then(() => {
              this.onButton();
            });
        })
        .catch(() => {
          // on cancel
        });
    },
    // 发送短信
    onMessage(mobile,message) {
      sendMessage(mobile, message).then((res) => {
        console.log(res.data);
      });
      this.show = false
      this.message = ''
    },
    // 
    showPopup(index) {
      this.show = true;
      this.index = index;
    },
  },
};
</script>
<style lang='scss' scoped>
.cell {
  font-size: 12px;
  text-align: left;
  padding: 5px 0px;
  color: #344357;
  .cell-left {
    width: 100px;
    flex: initial;
    color: #91a0b2;
  }
  .cell-right {
    text-align: left;
    color: #344357;
  }
}
//
.user {
  min-height: 100vh;
  background: #f3f3f3;
  &-search {
    z-index: 99;
    position: fixed;
    top: 0px;
    left: 0px;
    padding: 0px 28px;
    width: 100%;
    height: 137px;
    background: #344357;
    overflow: hidden;
    box-sizing: border-box;
    &-title {
      text-align: left;
      padding-top: 35px;
      padding-bottom: 20px;
      font-size: 20px;
      color: white;
    }
    &-input {
      display: flex;
      justify-content: space-between;
      margin: auto;
      .input {
        width: 240px;
        height: 36px;
        padding-top: 8px;
        padding-bottom: 8px;
        border-radius: 4px;
        border: #91a0b2;
      }
      .button {
        width: 64px;
        height: 36px;
      }
    }
  }
  &-item {
    overflow: hidden;
    margin-top: 135px;
    &-content {
      margin-top: 20px;
      width: 375px;
      // height: 228px;
      background: white;
      .title {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 320px;
        height: 43px;
        margin: 0 auto;
        border-bottom: 1px solid #707070;
        font-weight: Medium;
        &-name {
          font-size: 18px;
          color: #344357;
          font-weight: 500;
        }
        &-phone {
          font-size: 14px;
          color: #91a0b2;
        }
      }
      .context {
        padding-top: 6px;
        position: relative;
        width: 320px;
        height: 105px;
        margin: 0 auto;
        border-bottom: 1px solid #707070;
        &-left {
        }
        &-right {
          position: absolute;
          top: 16px;
          right: 0px;
          font-size: 14px;
          color: white;
          line-height: 36px;
          width: 90px;
          height: 36px;
          border-radius: 18px;
        }
      }
      .button {
        padding-top: 6px;
        width: 320px;
        margin: 0 auto;
      }
    }
  }
  .bg {
    // height: calc(100% - 137px);
    // position: relative;
    img {
      width: 185px;
      height: 176px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
  .message {
    width: 320px;
    // height: 284px;
    border-radius: 20px;
    overflow: hidden;
    &-title {
      box-sizing: border-box;
      padding-left: 20px;
      text-align: left;
      display: flex;
      align-items: center;
      width: 100%;
      height: 50px;
      background: #6f7b8a;
      font-size: 14px;
      color: white;
      line-height: 50px;
    }
    &-content {
      width: 100%;
      height: 234px;
      .text {
        width: 290px;
        height: 163px;
        background: #f6f6f6;
        margin: 12px auto;
      }
      .button {
        width: 148px;
        height: 36px;
      }
    }
  }
}
</style>
