import request from '../utils/request'

export function vipListApi(mobile, page, size) {
    const data = {
        mobile,
        page,
        size
    }

    return request({
        url: '/super-vip/v1/queryVipPageList',
        method: 'post',
        data: data
    })


}

export function updateApi(mobile) {
    const data = {
        mobile
    }
    return request({
        url: '/super-vip/activateSuperVip',
        method: 'post',
        data: data
    })
}