<template>
  <div class="login">
    <div class="login-title">
      <div class="image">
        <img src="@/assets/login.png" />
      </div>

      <div class="text">
        <span>Sign In</span>
        <span>登录</span>
      </div>
    </div>
    <div class="login-submit">
      <van-form @submit="onSubmit">
        <van-field
          class="input"
          v-model="username"
          name="username"
          label='用户名'
          :clearable='true'
          placeholder="用户名"
          :rules="[{ required: true, message: '请填写用户名' }]"
        />
        <van-field
          class="input"
          v-model="password"
          type="password"
          name="password"
          label="密码"
          placeholder="密码"
          :clearable='true'
          :rules="[{ required: true, message: '请填写密码' }]"
        />
        <div style="margin: 28px 0px">
          <van-button round block type="info" native-type="submit">
            提交
          </van-button>
        </div>
      </van-form>
    </div>
  </div>
</template>
<script>
import { loginApi } from "@/api/login";
import { setToken, removeToken } from "@/utils/auth";

export default {
  data() {
    return {
      username: "",
      password: "",
    };
  },
  mounted() {
    removeToken();
  },
  methods: {
    onSubmit(values) {
      loginApi(values.username, values.password).then((res) => {
        if (res.data.errno === 0) {
          setToken(res.data.data);
          this.$router.push("/user");
        }
      });
    },
  },
};
</script>
<style lang='scss' scoped>
.login {
  padding: 34px 32px;
  .login-title {
    .image {
      text-align: right;
      img {
        text-align: right;
        width: 155px;
        height: 155px;
      }
    }

    .text {
      text-align: left;
      :first-child {
        font-size: 32px;
      }
      :last-child {
        margin-left: 7px;
        font-size: 16px;
      }
    }
  }
  .login-submit{
    margin-top: 20px;
    .input{
      padding: 20px 0px;
      font-size: 16px;
    }
  }
}
</style>